<script>
import Highcharts from "highcharts";
import axios from "axios";
import dataModule from "highcharts/modules/data";

import HC_exportData from "highcharts/modules/export-data";
HC_exportData(Highcharts);

dataModule(Highcharts);

export default {
  props: ["username","results"],
  data() {
    return {
      image: "",
      
      errors: [],
      csvdata: null,
      have_results: "none",
      images:null,
      updatedImages: [],
      selectedItem: "",
      mySeries: [10, 20, 30, 40, 50, 60],

      items: [
        { title: "Home", icon: "mdi-view-dashboard" },
        { title: "Input Data", icon: "mdi-forum" },
      ],

      graph1: Highcharts,
      chartOptions1: {
        plotOptions: {
          series: {
            // marker: {
            //   enabled: false,
            // },
          },
        },

        data: {
          csv: "",

          //'http://localhost:8080/insight/#/data.csv/',
          // enablePolling: true,
        },

        credits: {
          enabled: false,
        },

        chart: {
          type: "line",

          // backgroundColor: "transparent", //"#62ad53",
          // borderColor: "blue",
          // plotBorderColor: "black",
          //   selectionMarkerFill: "red",
        },
        title: {
          text: "Yearly Sales Trend",
        },

        subtitle: {
          text: "",
        },
        yAxis: {
          title: {
            text: "Sales",
          },
        },
      },
      graph2: Highcharts,
      chartOptions2: {
        plotOptions: {
          series: {
            marker: {
              enabled: false,
            },
          },
        },
        series: [
          {
            lineWidth: 1,
          },
          {
            type: "areaspline",
            color: "#c4392d",
            negativeColor: "#5679c4",
            fillOpacity: 0.5,
          },
        ],
        data: {
          csv: "",
          //"http://localhost:8080/insight/#/data.csv",
          //"https://demo-live-data.highcharts.com/vs-load.csv",
          complete: function (options) {
            options.series.push({
              name: "Trend",
              data: [
                { x: 1880, y: -0.4 },
                { x: 2014, y: 0.52 },
              ],
            });
          },
          //'http://localhost:8080/insight/#/data.csv/',
          enablePolling: true,

          //'https://demo-live-data.highcharts.com/vs-load.csv'

          // call back is needed because by default it will be undefined.
          // complete: function (options) {
          //   // Add another series to the output
          //   options.series.push({
          //     name: "Trend",
          //     data: [
          //       { x: 1880, y: -0.4 },
          //       { x: 2014, y: 0.52 },
          //     ],
          //     dashStyle: "dash",
          //   });
          // },
          // csvURL: window.location.origin + "/insight/data.csv",
        },
        chart: {
          type: "spline",
          // backgroundColor: "transparent", //"#62ad53",
          //   borderColor: "blue",
          plotBorderColor: "black",
          //   selectionMarkerFill: "red",
        },
        title: {
          text: "Global temperature change",
        },

        subtitle: {
          text:
            "Data module: options structure is modified before final rendering",
        },
      },
      graph3: Highcharts,
      chartOptions3: {
        chart: {
          type: "column",
        },
        data: {
          csv: "",
        },
        credits: {
          enabled: false,
        },
        title: {
          text: "Fruit Consumption",
        },
        yAxis: {
          title: {
            text: "Units",
          },
        },
      },
    };
  },

  methods: {
    goToPage(title) {
      if (title == "Input Data") {
        this.$router.push({
          name: "InputData",
          params: { username: this.username },
        });
      }
    },

    updateData() {
      this.images = this.results;
      // this.chartOptions1.series[0].data = this.mySeries;
      // this.chartOptions1.series[0].data.csv = this.$refs.dataC.innerHTML;
      this.chartOptions2.data.csvURL =
        "http://localhost:8080/insight/#/data.csv";

      axios
        .get("data.csv")
        .then((response) => {
          this.chartOptions2.data.csv = response.data;
          // console.log( "this is data.csv ==== "+response.data);
        })
        .catch((error) => {
          console.log(error);
        });
         axios
        .get("backendFinal/backend/static/images/final_predictions.png")
        .then((response) => {
          this.image = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .get("backendFinal/salesFile.csv")
        .then((response) => {
          this.chartOptions1.data.csv = response.data;

          console.log("this is salesFile.csv ==== " + response.data);
        })
        .catch((error) => {
          console.log(error);
        });
      this.chartOptions3.data.csv = this.$refs.data1.innerHTML; // This is based on input data of csv data but not from csv file
    },
    
    
  },
  mounted: function () {
    this.updateData();
      if(this.results != this.images){
        this.images = this.results;

      }
    console.log(window.location.origin + "/insight/#/DashboardPage");
  },
};
</script>

